/********* Tailles et positionnements ********/
.btn {
    height: 50px;
    width: 100px;

    color: white;

    border-radius: 5px;
}

.btn-style-1 {
    background-color: $mauve-color;
    border: 1px solid $prune-color;
    box-shadow: 3px 3px 3px mediumslateblue;
    
}
.btn-style-2 {
    background-color: $brdx-color;
    border: 1px solid $prune-color;
}
.confirm-div {/*cacher la div de confirmation de suppression*/
    height: 0px;

    overflow: hidden;// pour que le bouton compris dans cette div soit caché quand height = 0px
}

.pagination-ul {
    display: flex;

    list-style-type: none;
}
.pagination-ul li {
    margin-right: 20px;
}
