
.bottles-table {/* table comportant la liste des bouteilles */
  width: 80%;
  border-collapse: collapse;
}
    .bottles-table th {// la ligne d'en-tête
        height: 30px;

        border-top: 3px solid black;
        border-bottom: 3px solid black;
    }
    .bottles-table td {// les lignes suivantes
        height: 130px;
        width: 16.6%;

        text-align: center;

        border-top: 1px solid black;
        border-bottom: 1px solid black;

    }
        .bouteille {
            width: 100px;
        }
        .select-icon {
            width: 50px;
        }

    @media screen and (max-width: 718px) {/* ne plus afficher ces colonnes complémentaires sur petit écran */
        .plusColumn {
            display: none;
        }
    }


.bottle-selected-table {/* table des bouteilles sélectionnées */
    width: 90%;
    border-collapse: collapse;
}
    .first-column {
        width: 20%;
    }
    .second-column {
        width: 80%;
    }
    .bottle-selected-table td {
        height: 45px;// pour avoir une hauteur minimum sur les lignes

        border-top: 1px solid black;
        border-bottom: 1px solid black;
    }
    input {
        width: 100%;
    }
    textarea {
        width: 100%;
    }
    





